p {
        display: block;
        margin-block-start: 1em;
        margin-block-end: 1em;
        margin-inline-start: 0px;
        margin-inline-end: 0px;
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 14px;
        line-height: 1.428571429;
        color: #333333;
        line-height: 19px;
        

        
}

.article {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 14px;
    line-height: 1.428571429;
    color: #333333;
    padding-left: 15%;
    padding-right: 15%;

    
}

.image {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 65%;
}

blockquote {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 40px;
    margin-inline-end: 40px;
}

img {
    max-width: 100%;
    max-height: 100%;
}

.list {
    cursor: pointer;
    text-decoration: underline;
}

/* Refernces */
ol {
    display: block;
    list-style-type: decimal;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
    text-decoration: none;
}

ol a:link {
    text-decoration: none;
    color:  #e64503;
}

ol a:visited {
    text-decoration: none;
    color:  #e64503;
}

ol a:hover {
    text-decoration: none;
    color:  #e64503;
}

ol a:active {
    text-decoration: none;
    color:  #e64503;
}

.quote {
    color: #6a6a6a;
    font-size: 16;
}

.quoteText {
    font-size: large;
}

blockquote {

    border-left: 10px solid #ccc;
    margin: 1.5em 10px;
    padding: 0.5em 10px;
    quotes: "\201C""\201D""\2018""\2019";
  }
  blockquote:before {
    color: #ccc;
    content: open-quote;
    font-size: 4em;
    line-height: 0.1em;
    margin-right: 0.25em;
    vertical-align: -0.4em;
  }
  blockquote p {
    display: inline;
    line-height: 20px;
  }


